<template>
  <div class="contact-us">
    <div class="page page6">
      <div class="about-us">
        <div class="about-us-inner">
          <div class="com-name"></div>
          <div class="desc"></div>
          <div class="line">
            112
          </div>
          <div class="line">
            400-8610-855
          </div>
          <div class="line">
            hancui@hancfl.com
          </div>
          <div class="line">
            www.hancfl.com
          </div>
          <div class="qr-code">
            <div class="code code1"></div>
            <div class="code code2"></div>
          </div>
          <div class="code-tips"></div>
          <div class="send-message">
            <div class="left">
              <div class="title"></div>
              <div class="desc"></div>
            </div>
            <div class="right">
              <el-input v-model="name" placeholder=":" class="name"></el-input>
              <el-input v-model="mobile" placeholder=":" class="mobile"></el-input>
              <el-input v-model="content"
                        placeholder=":"
                        resize="none"
                        type="textarea"
                        class="content"></el-input>
              <div class="submit-btn-area">
                <div class="submit-btn" @click="submitMessage"></div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {
    messageApi
  } from '@/apis/index'

  export default {
    name: 'ContactUs',
    data() {
      return {
        name: '',
        mobile: '',
        content: ''
      }
    },
    methods: {
      submitMessage() {
        if (this.name.trim() === '' || this.mobile.trim() === '' || this.content.trim() === '') {
          this.$message({
            showClose: true,
            type: 'error',
            message: ''
          })
          return false
        }
        const params = {
          name: this.name,
          mobile: this.mobile,
          content: this.content
        }
        messageApi(params).then(res => {
          if (res.success) {
            this.$message({
              showClose: true,
              type: 'success',
              message: '~'
            })
          }
        })
      }
    }
  }
</script>

<style lang="scss" scoped>
  .page6 {
    padding-bottom: 0.6rem;
    .about-us {
      width: 100%;
      background-color: #FFFFFF;
      .about-us-inner {
        width: 12rem;
        height: 100%;
        padding-top: 2.13rem;
        margin: auto;
        position: relative;
        .com-name {
          font-size: 0.18rem;
          color: #004033;
          padding-bottom: 0.21rem;
        }
        .desc {
          font-size: 0.16rem;
          color: #004033;
          padding-bottom: 1.01rem;
          line-height: 1.4;
        }
        .line {
          font-size: 0.18rem;
          color: #004033;
          padding-bottom: 0.56rem;
        }
        .qr-code {
          display: flex;
          padding-top: 0.21rem;
          .code {
            width: 1.45rem;
            height: 1.39rem;
            margin-right: 0.42rem;
          }
          .code1 {
            background-image: url("~@IMG/qcode1.png");
            background-size: 100% 100%;
          }
          .code2 {
            background-image: url("~@IMG/qcode2.png");
            background-size: 100% 100%;
          }
        }
        .code-tips {
          font-size: 0.18rem;
          color: #004033;
          padding-left: 0.68rem;
          padding-top: 0.28rem;
        }
        .send-message {
          width: 7rem;
          height: 4.05rem;
          background-color: #069163;
          position: absolute;
          top: 4.68rem;
          right: 0;
          display: flex;
          border-radius: 0.05rem;
          opacity: 0.8;
          .left {
            width: 2.75rem;
            height: 100%;
            padding-top: 0.5rem;
            padding-left: 0.5rem;
            .title {
              font-size: 0.18rem;
              color: #FFFFFF;
              padding-bottom: 0.4rem;
            }
            .desc {
              font-size: 0.18rem;
              color: #FFFFFF;
              width: 1.47rem;
              line-height: 1.6;
            }
          }
          .right {
            flex: 1;
            height: 100%;
            padding-top: 0.6rem;
            padding-right: 0.5rem;
            /deep/ .el-input {
              width: 100%;
              height: 0.4rem;
              margin-bottom: 0.3rem;
            }
            .submit-btn-area {
              padding-top: 0.29rem;
              .submit-btn {
                width: 0.9rem;
                height: 0.4rem;
                float: right;
                background-color: #FFFFFF;
                text-align: center;
                line-height: 0.4rem;
                font-size: 0.18rem;
                color: #33A27D;
                cursor: pointer;
              }
            }
          }
        }
      }
    }
  }
</style>



